import { useState, useEffect, useCallback } from 'react';
import moment from 'moment';
import {
  Layout,
  Checkbox,
  Input,
  Form,
  DatePicker,
  Typography,
  Button,
  notification,
  TimePicker,
} from 'antd';

import {
  CopyOutlined,
  RedoOutlined,
} from '@ant-design/icons';

import './App.scss';
import { CopyToClipboard } from 'react-copy-to-clipboard/lib/Component';

const { Content } = Layout;
const { TextArea } = Input;
const { Text } = Typography;

function getRandomInt(max) {
  return Math.floor(Math.random() * Math.floor(max));
}

const ENABLE_LABELS = false;

function App() {
  const [dateEnabled, setDateEnabled] = useState(true);
  const [date, setDate] = useState(moment());
  const [time, setTime] = useState(moment());

  const [addEnabled, setAddEnabled] = useState(false);
  const [amendEnabled, setAmendEnabled] = useState(true);
  const [resetEnabled, setResetEnabled] = useState(true);
  const [noVerifyEnabled, setNoVerifyEnabled] = useState(true);

  const [messageEnabled, setMessageEnabled] = useState(false);
  const [message, setMessage] = useState('');

  const [result, setResult] = useState('');

  useEffect(() => {
    let newResult = 'git commit';

    if (amendEnabled) {
      newResult = `${newResult} --amend`;
    }

    if (noVerifyEnabled) {
      newResult = `${newResult} --no-verify`;
    }

    if (dateEnabled) {
      const dateString = date.format('YYYY-MM-DD');
      const timeString = time.format('HH:mm:ss');

      newResult = `GIT_COMMITTER_DATE="${dateString} ${timeString}" ${newResult} --date "${dateString} ${timeString}"`;
    }

    if (messageEnabled && message) {
      let messageResult = '';
      const lines = message.split(/\r?\n/);

      for (let i = 0; i < lines.length; i++) {
        if (!!lines[i].trim()) {
          messageResult = `${messageResult} -m "${lines[i].replaceAll('"', '\\"')}"`;
        }
      }

      newResult = `${newResult} ${messageResult}`;
    }

    if (resetEnabled) {
      newResult = `git reset && ${newResult}`;
    }

    if (addEnabled) {
      newResult = `git add . && ${newResult}`;
    }

    setResult(newResult);
  }, [dateEnabled, date, time, addEnabled, amendEnabled, resetEnabled, message, messageEnabled, noVerifyEnabled]);

  useEffect(() => {
    if (addEnabled) {
      setResetEnabled(false);
    }
  }, [addEnabled, setResetEnabled]);

  useEffect(() => {
    if (resetEnabled) {
      setAddEnabled(false);
    }
  }, [resetEnabled, setAddEnabled]);

  useEffect(() => {
    if (!amendEnabled) {
      setResetEnabled(false);
    }
  }, [amendEnabled, setResetEnabled]);

  const handleRandomHh = useCallback(() => {
    const newTime = time?.clone() || moment();
    newTime.hours(getRandomInt(12) + 10);

    setTime(newTime);
  }, [time, setTime]);

  const handleRandomMm = useCallback(() => {
    const newTime = time?.clone() || moment();
    newTime.minutes(getRandomInt(60));

    setTime(newTime);
  }, [time, setTime]);

  const handleRandomSs = useCallback(() => {
    const newTime = time?.clone() || moment();
    newTime.seconds(getRandomInt(60));

    setTime(newTime);
  }, [time, setTime]);

  const handleSetNow = () => {
    setTime(moment());
    setDate(moment());
  };

  return (
    <Layout className="app">
      <Content className="content-wrapper">
        <Layout className="content-layout">
          <Content className="content">
            <Form layout="vertical">
              <Form.Item label={ENABLE_LABELS ? '▸ Date' : null}>
                <Checkbox
                  checked={dateEnabled}
                  onChange={e => setDateEnabled(e.target.checked)}
                >
                  Add date/time:
                </Checkbox>

                <DatePicker
                  disabled={!dateEnabled}
                  value={date}
                  onChange={setDate}
                  format={'YYYY-MM-DD'}
                  allowClear={false}
                />
                &nbsp;
                <TimePicker
                  disabled={!dateEnabled}
                  value={time}
                  onChange={setTime}
                  allowClear={false}
                />
                &nbsp;
                <Button
                  onClick={handleRandomHh}
                  disabled={!dateEnabled}
                >
                  <RedoOutlined />h
                </Button>
                &nbsp;
                <Button
                  onClick={handleRandomMm}
                  disabled={!dateEnabled}
                >
                  <RedoOutlined />m
                </Button>
                &nbsp;
                <Button
                  onClick={handleRandomSs}
                  disabled={!dateEnabled}
                >
                  <RedoOutlined />s
                </Button>
                &nbsp;
                <Button
                  onClick={handleSetNow}
                  disabled={!dateEnabled}
                >
                  Now!
                </Button>
              </Form.Item>

              <Form.Item label={ENABLE_LABELS ? '▸ Amend' : null}>
                <Checkbox
                  checked={amendEnabled}
                  onChange={e => setAmendEnabled(e.target.checked)}
                >
                  Enable Amend <Text type="secondary">(Disable to make new commit)</Text>
                </Checkbox>
              </Form.Item>

              <Form.Item label={ENABLE_LABELS ? '▸ Add .' : null}>
                <Checkbox
                  checked={addEnabled}
                  onChange={e => setAddEnabled(e.target.checked)}
                >
                  Enable Add . <Text type="secondary">(Include un-committed files)</Text>
                </Checkbox>
              </Form.Item>

              <Form.Item label={ENABLE_LABELS ? '▸ Reset' : null}>
                <Checkbox
                  disabled={!amendEnabled}
                  checked={resetEnabled}
                  onChange={e => setResetEnabled(e.target.checked)}
                >
                  Enable Reset <Text type="secondary">(Exclude un-committed files)</Text>
                </Checkbox>
                <br />
                <Text type="secondary">
                  This does not change your working tree, just resets HEAD, so that --amend commit won't add new
                  files and only change date or message
                </Text>
              </Form.Item>

              <Form.Item label={ENABLE_LABELS ? '▸ No Hooks!' : null}>
                <Checkbox
                  checked={noVerifyEnabled}
                  onChange={e => setNoVerifyEnabled(e.target.checked)}
                >
                  Disable Hooks
                </Checkbox>
              </Form.Item>

              <Form.Item label={ENABLE_LABELS ? '▸ Message' : null}>
                <Checkbox
                  checked={messageEnabled}
                  onChange={e => setMessageEnabled(e.target.checked)}
                >
                  Enable Message
                </Checkbox>
                <br />
                <TextArea
                  value={message}
                  rows={2}
                  onChange={e => setMessage(e.target.value)}
                  disabled={!messageEnabled}
                />
              </Form.Item>

              <Form.Item label={'▸ Result'}>
                <CopyToClipboard
                  text={result}
                  readOnly
                  onCopy={() => notification.success({ message: 'Copied to clipboard!' })}
                >
                  <Button type="primary" className="copy-button"><CopyOutlined /></Button>
                </CopyToClipboard>

                <TextArea value={result} rows={2} className="result-text" />
              </Form.Item>
            </Form>
          </Content>
        </Layout>
      </Content>
    </Layout>
  );
}

export default App;
